"use client";

import React, { useState, useEffect, useCallback } from "react";
import { useAuth } from "@/hooks/useAuth";
import { useRouter } from "next/navigation";
import { ChevronLeftIcon, DollarLineIcon, CheckCircleIcon } from "@/icons";
import { formatCurrency } from "@/utils/format";
import { vendaFinalService, type VendaFinal, StatusVendaFinal, TipoPagamento } from "@/services/api/venda.service";
import Input from "@/components/form/input/InputField";
import Select from "@/components/ui/Select";
import Pagination from "@/components/ui/Pagination";
import Label from "@/components/form/Label";
import DatePicker from "@/components/form/date-picker";
import FaturaCard from "@/components/cliente/FaturaCard";
import QRCodeModal from "@/components/cliente/QRCodeModal";
import { ThemeToggleButton } from "@/components/common/ThemeToggleButton";
import toast from "react-hot-toast";

type FilterOptions = {
  status?: StatusVendaFinal;
  type?: TipoPagamento;
  vencimento?: Date;
  orderBy?: 'vencimento_asc' | 'vencimento_desc' | 'valor_asc' | 'valor_desc' | 'createdAt_asc' | 'createdAt_desc';
};

export default function FaturasPage() {
  const { user } = useAuth();
  const router = useRouter();
  const [faturas, setFaturas] = useState<VendaFinal[]>([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(0);
  const [filters, setFilters] = useState<FilterOptions>({});
  const [itemsPerPage, setItemsPerPage] = useState(9);
  const [showQRCode, setShowQRCode] = useState(false);
  const [selectedPixCode, setSelectedPixCode] = useState<string>("");
  const [selectedQRCode, setSelectedQRCode] = useState<string>("");

  const loadFaturas = useCallback(async () => {
    try {
      setLoading(true);
      
      const params = {
        page,
        limit: itemsPerPage,
        search: searchTerm || undefined,
        status: filters.status,
        type: filters.type,
        vencimento: filters.vencimento,
        orderBy: filters.orderBy || "vencimento_desc"
      };

      const response = await vendaFinalService.list(params);
      setFaturas(response.data);
      setTotalPages(response.pagination?.pages || 1);
    } catch (error) {
      console.error('Erro ao carregar faturas:', error);
      toast.error('Erro ao carregar faturas');
    } finally {
      setLoading(false);
    }
  }, [page, searchTerm, filters, itemsPerPage]);

  useEffect(() => {
    loadFaturas();
  }, [loadFaturas]);

  const handleSearch = (event: React.ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(event.target.value);
    setPage(1);
  };

  const handleFilterChange = (newFilters: Partial<FilterOptions>) => {
    setFilters(prev => ({ ...prev, ...newFilters }));
    setPage(1);
  };

  const handleShowQRCode = (pixCode: string, qrCode?: string) => {
    setSelectedPixCode(pixCode);
    setSelectedQRCode(qrCode || "");
    setShowQRCode(true);
  };

  const handleGenerateSuccess = async () => {
    // Recarregar as faturas após gerar uma nova
    await loadFaturas();
  };

  const statusOptions = [
    { value: 'PENDING', label: 'Pendente' },
    { value: 'APPROVED', label: 'Pago' },
    { value: 'CANCELLED', label: 'Cancelado' }
  ];

  const typeOptions = [
    { value: 'PIX', label: 'PIX' },
    { value: 'BOLETO', label: 'Boleto' }
  ];

  const orderByOptions = [
    { value: 'vencimento_asc', label: 'Vencimento - Mais próximo' },
    { value: 'vencimento_desc', label: 'Vencimento - Mais distante' },
    { value: 'valor_desc', label: 'Valor - Maior' },
    { value: 'valor_asc', label: 'Valor - Menor' },
    { value: 'createdAt_desc', label: 'Criado - Mais recente' },
    { value: 'createdAt_asc', label: 'Criado - Mais antigo' }
  ];

  if (loading && faturas.length === 0) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <button
              onClick={() => router.back()}
              className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
              aria-label="Voltar"
            >
              <ChevronLeftIcon className="w-6 h-6 text-gray-600 dark:text-gray-400" />
            </button>
            <div>
              <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
                Minhas Faturas
              </h1>
              <p className="text-gray-600 dark:text-gray-400 mt-1">
                Visualize todas as suas faturas e pagamentos
              </p>
            </div>
          </div>
          <div className="flex items-center space-x-2">
            <ThemeToggleButton />
            <DollarLineIcon className="w-8 h-8 text-blue-600 dark:text-blue-400" />
          </div>
        </div>
      </div>

      {/* Filtros */}
      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
        <div className="flex flex-col sm:flex-row items-center gap-4">
          <div className="flex-1 w-full sm:max-w-xs">
            <Input
              type="text"
              placeholder="Buscar faturas..."
              value={searchTerm}
              onChange={handleSearch}
              className="w-full"
            />
          </div>
          <div className="flex flex-wrap gap-3">
            <Select
              placeholder="Status"
              options={statusOptions}
              value={statusOptions.find(opt => opt.value === filters.status)}
              onChange={(option) => handleFilterChange({ 
                status: option ? (option as { value: StatusVendaFinal }).value : undefined 
              })}
              isClearable
              className="whitespace-nowrap min-w-40"
            />
            <Select
              placeholder="Tipo"
              options={typeOptions}
              value={typeOptions.find(opt => opt.value === filters.type)}
              onChange={(option) => handleFilterChange({ 
                type: option ? (option as { value: TipoPagamento }).value : undefined 
              })}
              isClearable
              className="whitespace-nowrap min-w-40"
            />
            <div className="min-w-48">
              <DatePicker
                id="vencimento"
                onChange={(selectedDates: Date[]) => {
                  handleFilterChange({ vencimento: selectedDates[0] || undefined });
                }}
                defaultDate={filters.vencimento}
              />
            </div>
            <Select
              placeholder="Ordenar por"
              options={orderByOptions}
              value={orderByOptions.find(opt => opt.value === filters.orderBy)}
              onChange={(option) => handleFilterChange({ 
                orderBy: option ? (option as { value: string }).value as any : undefined
              })}
              isClearable
              className="whitespace-nowrap min-w-50"
            />
          </div>
        </div>
      </div>

      {/* Controles de Paginação */}
      <div className="flex justify-between mb-4 gap-2 flex-wrap">
        <div className='flex items-center gap-0.5'>
          <Label className='flex items-center mb-0 h-full'>Quantidade por página</Label>
          <Input
            className='max-w-24'
            defaultValue={itemsPerPage}
            max={100}
            min={1}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
              const newValue = Number(e.target.value) || 9;
              setItemsPerPage(newValue);
              setPage(1);
            }}
          />
        </div>
        <Pagination
          currentPage={page}
          totalPages={totalPages}
          onPageChange={setPage}
        />
      </div>

      {/* Lista de Faturas */}
      {loading ? (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {[1, 2, 3].map((n) => (
            <div key={n} className="animate-pulse bg-gray-100 dark:bg-gray-800 rounded-lg h-48" />
          ))}
        </div>
      ) : (
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {faturas.length > 0 ? (
            faturas.map((fatura) => (
              <FaturaCard
                key={fatura.id}
                vendaFinal={fatura}
                onShowQRCode={handleShowQRCode}
                onGenerateSuccess={handleGenerateSuccess}
              />
            ))
          ) : (
            <div className="col-span-full text-center py-12">
              <CheckCircleIcon className="w-16 h-16 text-gray-400 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
                Nenhuma fatura encontrada
              </h3>
              <p className="text-gray-600 dark:text-gray-400">
                Você ainda não possui faturas cadastradas.
              </p>
            </div>
          )}
        </div>
      )}

      {/* Paginação */}
      {totalPages > 1 && (
        <div className="mt-6">
          <div className="flex justify-end mb-4 gap-2 flex-wrap">
            <Pagination
              currentPage={page}
              totalPages={totalPages}
              onPageChange={setPage}
            />
          </div>
        </div>
      )}

      {/* Modal QR Code */}
      <QRCodeModal
        isOpen={showQRCode}
        onClose={() => setShowQRCode(false)}
        pixCode={selectedPixCode}
        qrCode={selectedQRCode}
      />
    </div>
  );
}
